@import "../../../style/theme/default/var.css";

@component-namespace institution{
  @b form{
    @e cut{
      border-bottom:1px solid var(--theme-border-color-primary);
      margin-bottom:var(--theme-margin-small);
    }
  }

  @b rating{
    & >  .el-form-item__content{
      margin-left:0 !important;
    }

    & .el-form-item {
        margin-bottom: 10px;
      }

    @e property{

      padding: var(--theme-padding-small) 0;
    }

    @e tabs{
      margin-bottom:var(--theme-margin-base);
      &.el-tabs--border-card{
         box-shadow:none;
          & > .el-tabs__content{
            padding: 0 0;
          }
    }

      &  .el-tabs__item {
           padding:0 var(--theme-padding-base);
           vertical-align: middle;
         }
    .el-tabs__nav-custom{
      display: inline-block;
      vertical-align: middle;
    }
    .rating-input{
      margin-left: var(--theme-margin-base);
      margin-top: 2px;
      margin-bottom: 2px;
      & .el-button {
          border:1px dashed var(--theme-color-primary);
      }
    }

  .rating-sort-btn {
    margin-top: 2px;
    margin-bottom: 2px;
  }
    .rating-title{
      & i {
          font-size:var(--theme-font-size-small);
          transform: scale(0.8,0.8);
        }
      }
      .rating-name{

      }
      .rating-edit{

      }

      }

   }

  @b sort {
    @e list{
     @utils-clearfix;
      & .sort-item{
        padding: var(--theme-padding-small) 20px var(--theme-padding-small) 0;
        position:relative;
        cursor:move;
        &.sortable-chosen {
          background-color: var(--theme-background-color-primary);
         }
      }
  & .sort-icon{
    position: absolute;
    /*top:50%;*/
    transform:translate(-50%, 0);
    right: 0;
    }

    }
  }

}
.tmp-popover-add{
  .el-checkbox{
    width:100%;
    height: 18px;
  @utils-ellipsis;
  }
  .el-checkbox:first-child{
    margin-left: var(--theme-margin-base);
  }
  .el-checkbox + .el-checkbox{
    margin-left: var(--theme-margin-base);
  }
  .order-popover-footer{
    padding-top:5px;
    text-align: right;
  }
  .el-checkbox__label {
    font-size: var(--theme-font-size-small);
  }
}
.tmp-popover-add-footer{
  padding-top: 5px;
  text-align: right;
}



